import path from 'node:path';
import fs from 'node:fs';
import crypto from 'node:crypto';
import mime from 'mime-types';
import { DeleteObjectsCommand, HeadObjectCommand, ListObjectsV2Command, PutObjectCommand, } from '@aws-sdk/client-s3';
import glob from '@actions/glob';
import { info } from '@actions/core';
import minimatch from 'minimatch';
import { workspace } from './github.js';
export async function getObjectMetadata(client, s3BucketName, key) {
    try {
        return await client.send(new HeadObjectCommand({
            Bucket: s3BucketName,
            Key: key,
        }));
    }
    catch (e) {
        return undefined;
    }
}
export function getObjectKeyFromFilePath(rootDir, absoluteFilePath, prefix, stripExtensionGlob) {
    const key = path.join(prefix, path.relative(rootDir, absoluteFilePath));
    const { root, dir, name, ext } = path.parse(key);
    const extensionLessFile = path.join(root + dir, name);
    const matchesStripExtension = stripExtensionGlob
        ? minimatch(absoluteFilePath, stripExtensionGlob)
        : false;
    if (matchesStripExtension) {
        return extensionLessFile;
    }
    return extensionLessFile + ext;
}
function getContentTypeForExtension(extension) {
    const contentType = mime.lookup(extension);
    if (contentType === false) {
        throw new Error(`Unable to detect content-type for ${extension}`);
    }
    return contentType;
}
async function uploadFile(client, s3BucketName, key, absoluteFilePath, cacheControl, contentType, acl) {
    return client.send(new PutObjectCommand({
        Bucket: s3BucketName,
        Key: key,
        CacheControl: cacheControl,
        ContentType: contentType,
        ACL: acl,
        Body: fs.createReadStream(absoluteFilePath),
    }));
}
function getETag(absoluteFilePath) {
    const fileContents = fs.readFileSync(absoluteFilePath, 'utf-8');
    const base64ETag = Buffer.from(crypto.createHash('md5').update(fileContents).digest('hex'), 'base64').toString('base64');
    return JSON.stringify(base64ETag);
}
export async function maybeUploadFile(client, s3BucketName, absoluteFilePath, key, cacheControl, acl) {
    const extension = path.extname(absoluteFilePath).toLowerCase();
    const contentType = getContentTypeForExtension(extension);
    const eTag = getETag(absoluteFilePath);
    const metadata = await getObjectMetadata(client, s3BucketName, key);
    const shouldUploadFile = !metadata ||
        metadata.CacheControl !== cacheControl ||
        metadata.ContentType !== contentType ||
        metadata.ETag !== eTag;
    if (shouldUploadFile) {
        await uploadFile(client, s3BucketName, key, absoluteFilePath, cacheControl, contentType, acl);
    }
    return shouldUploadFile;
}
export async function getFilesFromSrcDir(srcDir, filesGlob) {
    if (srcDir.trim() === '' || filesGlob.trim() === '') {
        throw new Error('srcDir and filesGlob must not be empty');
    }
    const globber = await glob.create(`${srcDir}/${filesGlob}`, {
        matchDirectories: false,
    });
    return globber.glob();
}
export async function syncFilesToS3(client, s3BucketName, srcDir, filesGlob, prefix, stripExtensionGlob, cacheControl, acl) {
    if (!workspace) {
        throw new Error('GITHUB_WORKSPACE is not defined');
    }
    const rootDir = path.join(workspace, srcDir);
    const files = await getFilesFromSrcDir(srcDir, filesGlob);
    const uploadedKeys = [];
    for (const file of files) {
        const key = getObjectKeyFromFilePath(rootDir, file, prefix, stripExtensionGlob);
        const uploaded = await maybeUploadFile(client, s3BucketName, file, key, cacheControl, acl);
        if (uploaded) {
            info(`Synced ${key}`);
            uploadedKeys.push(key);
        }
        else {
            info(`Skipped ${key} (no change)`);
        }
    }
    info(`Synced ${uploadedKeys.length} files with cache-control: ${cacheControl}`);
    return uploadedKeys;
}
